#editor-input {
  position: relative;
}

#editor-input input {
  width: 100%;
  height: 48px;
  margin: 0;
  padding: 0 16px;
  display: block;
  position: relative;
  border-style: none;
  background: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font: inherit;
  -ms-flex-positive: 1;
      flex-grow: 1;
  z-index: 1;
}

#editor-input input:focus {
  outline: none;
}

#editor-predicates + #editor-input input {
  padding-left: 10px;
}

#editor-predicates + #editor-input #ghost {
  padding-left: 10px;
}



